import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';
import Divider from '../../common/Divider';

import psf0 from '../../../pdf/figs/psf0.PNG';
import psf1 from '../../../pdf/figs/psf1.PNG';
import psf2 from '../../../pdf/figs/psf2.PNG';
import psf3 from '../../../pdf/figs/psf3.PNG';


import droughts1 from '../../../pdf/figs/droughts1.PNG';
import droughts2 from '../../../pdf/figs/droughts2.PNG';

import caps1 from '../../../pdf/figs/caps1.PNG';
import caps2 from '../../../pdf/figs/caps2.PNG';
import caps3 from '../../../pdf/figs/caps3.PNG';


import audits1 from '../../../pdf/figs/audits1.PNG';
import audits2 from '../../../pdf/figs/audits2.PNG';
import audits3 from '../../../pdf/figs/audits3.PNG';

import acc1 from '../../../pdf/figs/acc.PNG';

import evt1 from '../../../pdf/figs/evt1.PNG';
import evt2 from '../../../pdf/figs/evt2.PNG';
import evt3 from '../../../pdf/figs/evt3.PNG';

import lib1 from '../../../pdf/figs/lib1.PNG';
import lib2 from '../../../pdf/figs/lib2.PNG';
import lib3 from '../../../pdf/figs/lib3.PNG';

import cons1 from '../../../pdf/figs/cons1.PNG';

import mita1 from '../../../pdf/figs/mita_map_1.PNG';
import mita2 from '../../../pdf/figs/mita_map_2.PNG';

import class2 from '../../../pdf/figs/class2.PNG';



const NiceFiguresTables = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="nice-figures-tables">
      <Title>Brazilian Policy Walkthrough</Title>
      <div>
      <span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Read me{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ? "The sequence of figures and tables below try to provide information on what I consider to be the most impressive results in the applied social sciences of Brazilian public policies or social programs, more generally. At the end of the sequence, there are some other figures from papers I like that are not from Brazil." : ''}
     </div>     

      <div className="area">I. Health Policy</div>

      <p>I.1. "Programa Saúde da Família" (PSF)</p>

      <span><p>I.1.i. Rocha, Romero; Soares, Rodrigo (2010). <a href="https://onlinelibrary.wiley.com/doi/abs/10.1002/hec.1607" target="blank">"Evaluating the Impact of Community‐based Health Interventions: Evidence from Brazil's Family Health Program"</a>. Health Economics.</p></span>

      <div><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ? "This paper analyzes the direct and indirect impacts of Brazil's Family Health Program, using municipality level mortality data from the Brazilian Ministry of Health, and individual level data from the Brazilian household survey. We estimate the effects of the program on mortality and on household behavior related to child labor and schooling, employment of adults, and fertility. We find consistent effects of the program on reductions in mortality throughout the age distribution, but mainly at earlier ages. Municipalities in the poorest regions of the country benefit particularly from the program. For these regions, implementation of the program is also robustly associated with increased labor supply of adults, reduced fertility, and increased school enrollment. Evidence suggests that the Family Health Program is a highly cost‐effective tool for improving health in poor areas.": ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure from Paper, PSF Rollout Across Municipalities{abstractIsVisible ? ': ' : " (click to expand)"}</span>{abstractIsVisible ? <p><img src={psf0} alt="graph" /></p>: ''}
      </div>


      <span><p>I.1.ii. Balhotra, Sonia; Rocha, Romero; Soares, Rodrigo (2019). <a href=" https://cdep.sipa.columbia.edu/sites/default/files/cdep/WP72Soares-2.pdf" target="blank">"Can Universalization of Health Work? Evidence from Health Systems Restructuring and Expansion in Brazil"</a>. Manuscript. </p></span>


      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Manuscript Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "''In a bold move, Brazil constitutionalized the right to health and acted to universalize access to fully subsidized health care. We use rich administrative data to investigate the restructuring of the system, which moved away from specialized services centralized in hospitals, and toward expansions in primary care. We show that this movement led to increases in access to primarycare and hospital services, alongside large reductions in maternal, fetal, and infant mortality. We also show that these improvements relied heavily on the complementarities across different layers of the health system and that they reduced health inequalities across socioeconomic groups. Brazil presents a success story with potentially important lessons for other countries considering  universal health coverage.''": ''}</p></span>
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure from Manuscript, PSF Implementation{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? <p><img src={psf1} alt="graph" /></p>: ''}</p></span>
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure from Manuscript, Dynamic Treatment Effects of PSF on Female Mortality{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? <p><img src={psf2} alt="graph" /></p>: ''}</p></span>
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure from Paper, Dynamic Treatment Effects of PSF on Infant Mortality{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? <p><img src={psf3} alt="graph" /></p>: ''}</p></span>
      </div>




      <Divider />


      <p>I.2. Rainfall Variation and Droughts in the Semiarid</p>
      <span><p>I.2.1. Rocha, Rudi; Soares, Rodrigo (2015). <a href="https://www.sciencedirect.com/science/article/abs/pii/S0304387814001096" target="blank">"Water Scarcity and Birth Outcomes in the Brazilian Semiarid"</a>. Journal of Development Economics.</p></span>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "Roughly one-third of the rural population in developing countries lives in arid and semiarid regions, facing recurrentwater scarcity. This is likely to become an even more common situationwith climate change. This paper analyzes the impact of rainfall fluctuations during the gestational period on health at birth in the Brazilian semiarid, highlighting the role of water scarcity as a determinant of early life health. We find that negative rainfall shocks are robustly correlated with higher infantmortality, lower birth weight, and shorter gestation periods. Mortality effects are concentrated on intestinal infections andmalnutrition, and are greatlyminimizedwhen the local public health infrastructure is sufficiently developed (municipality coverage of piped water and sanitation). We also find that effects are stronger during the fetal period (2nd trimester of gestation), for children born during the dry season, and for mortality immediately after birth. Our estimates suggest that expansions in public health infrastructure would be a cost-effective way of reducing the response of infant mortality to rainfall scarcity.": ''}</p></span>
      </div>


      
      <p><img src={droughts1} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      <p><img src={droughts2} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Table{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      <Divider />


      <p>I.3. "Centros de Atenção Psicossocial" (CAPS) </p>
      <span><p>I.3.1. Dias, Mateus; Fontes, Luiz Felipe (2015). "The Effects of a Large-Scale Mental Health Reform: Evidence from Brazil". Manuscript.</p></span>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Manuscript Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "''This paper assesses the effects of the introduction of Psychosocial Care Centers (CAPSs) in Brazil on mental health. These units are the centerpiece of the Brazilian psychiatric reform, meant to deliver community-based mental health services for people with moderate or severe disorders, including substance abuse. Using a differences-in-differences design that exploits the roll-out of the CAPSs across the country, we show that these centers improved access and utilization of outpatient mental health care and reduced hospital admissions due to mental and behavioral disorders. Those reductions were more pronounced for long-stay admissions and among patients with schizophrenia. We also find that the introduction of centers delivering substance abuse treatment reduced deaths caused by alcoholic liver disease. Despite these positive effects, our evidence indicates that this shift away from inpatient care increased homicide rates.''": ''}</p></span>
      </div>

      <p><img src={caps1} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      
      <p><img src={caps2} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      <p><img src={caps3} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      <Divider />

      <p>4. "Aqui Tem Farmácia Popular" (ATFP) </p>
      <span><p>Pedro, Américo; Rocha, Rudi (2020). "Subsidizing Access to Prescription Drugs and Health Outcomes: The Case of Diabetes". Journal of Health Economics.</p></span>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "''This paper evaluates the health effects of a large-scale subsidizing program of prescription drugs introduced in Brazil, the Aqui Tem Farmácia Popular program (ATFP). We exploit features of the program to identify its effects on mortality and hospitalization rates by diabetes for individuals aged 40 years or more. We find weak evidence for a decline in mortality, but a robust reduction in hospitalization rates. According to our preferred specification, an additional ATFP pharmacy per 100,000 inhabitants is associated with a decrease in hospitalization rates by diabetes of 8.2, which corresponds to 3.6% of its baseline rate. Effects are larger for Type II diabetes in comparison to Type I, and among patients with relatively lower socioeconomic status. Overall, the results are consistent with insulin-dependent patients being relatively less responsive to subsidies because of higher immediate life-threatening risks; and with lower-SES individuals being more responsive because of liquidity constraints. These results support the view that the optimal design of health systems and cost-sharing mechanisms should take into account equity concerns, heterogeneous impacts by health condition, and their potential offsetting effects on the utilization of downstream health services.''": ''}</p></span>
      </div>
      

      <Divider />


      <div className="area">II. Educational Policy</div>



      <Divider />

      <div className="area">III. Crime and Conflict</div>




      <Divider />


      <div className="area">IV. Politics and Governance</div>


     <p> IV.1. "Programa de Fiscalização a Partir de Sorteios Públicos"</p>
      <span><p>Ferraz, Claudio; Finan, Frederico (2008). "Exposing Corrupt Politicians: The Effect of Brazil’s Publicly Released Audits on Electoral Outcomes". Quarterly Journal of Economics.</p></span>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "''This paper uses publicly released audit reports to study the effects of disclosing information about corruption practices on electoral accountability. In 2003, as part of an anticorruption program, Brazil's federal government began to select municipalities at random to audit their expenditures of federally transferred funds. The findings of these audits were then made publicly available and disseminated to media sources. Using a data set on corruption constructed from the audit reports, we compare the electoral outcomes of municipalities audited before versus after the 2004 elections, with the same levels of reported corruption. We show that the release of the audit outcomes had a significant impact on incumbents' electoral performance, and that these effects were more pronounced in municipalities where local radio was present to divulge the information. Our findings highlight the value of having a more informed electorate and the role played by local media in enhancing political selection.''": ''}</p></span>
        </div>

      <p><img src={audits1} alt="graph" /></p>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ':' : ""}</span>
        <span><p>{abstractIsVisible ? "On the 2004 elections, the reelection rate of mayors involved in corruption detected by the audits was lower. This impact was monotonic in the number of acts of corruption, that is, mayors with more than 2 acts of corruption seem to have been more punished in the polls. Mayors with 0 acts of corruption, in turn, seem to have captured electoral advantage. Overall, these results highligh the value of having a more informed electorate to enhance political selection. A summary of these results can be found in the figure below.": ''}</p></span>
      </div>


      <span><p>Avis, Eric; Ferraz, Claudio; Finan, Frederico (2018). "Do Government Audits Reduce Corruption? Estimating the Impacts of Exposing Corrupt Politicians". Journal of Political Economy.</p></span>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "''This paper examines the extent to which government audits of public resources can reduce corruption by enhancing political and judiciary accountability. We do so in the context of Brazil’s anticorruption program, which randomly audits municipalities for their use of federal funds. We find that being audited in the past reduces future corruption by 8 percent, while also increasing the likelihood of experiencing a subsequent legal action by 20 percent. We interpret these reducedform findings through a political agency model, which we structurally estimate. Our results suggest that the reduction in corruption comes mostly from the audits increasing the perceived nonelectoral costs of engaging in corruption.''": ''}</p></span>
        </div>

      <p><img src={audits3} alt="graph" /></p>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ':' : ""}</span>
        <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>

      <span><p>Colonelli, Emanuele; Prem, Mounu (2021). "Corruption and Firms". Review of Economic Studies.</p></span>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "''We estimate the causal real economic effects of a randomized anti- corruption crackdown on local governments in Brazil using rich micro-data on corruption and firms. After anti-corruption audits, municipalities experience an in- crease in the number of firms concentrated in sectors most dependent on government relationships. Through the estimation of geographic spillovers and additional tests, we show that audits operate via both a direct detection effect as well as through indirect deterrence channels. Politically connected firms suffer after the audits. Our estimates indicate the anti-corruption program generates significant local multipliers which are consistent with the presence of a large corruption tax on government-dependent firms.''": ''}</p></span>
        </div>


      <p><img src={audits2} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ':' : ""}</span>
        <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>      


      <p>2. Mayor Term Limits as a Political Institution </p>

      <span><p>Ferraz, Claudio; Finan, Frederico (2011). "Electoral Accountability and Corruption: Evidence from the Audits of Local Governments". American Economic Review.</p></span>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "''We show that political institutions affect corruption levels. We use audit reports in Brazil to construct new measures of political corruption in local governments and test whether electoral accountability affects the corruption practices of incumbent politicians. We find significantly less corruption in municipalities where mayors can get reelected. Mayors with reelection incentives misappropriate 27 percent fewer resources than mayors without reelection incentives. These effects are more pronounced among municipalities with less access to information and where the likelihood of judicial punishment is lower. Overall our findings suggest that electoral rules that enhance political accountability play a crucial role in constraining politician's corrupt behavior.''": ''}</p></span>
        </div>

        <p><img src={acc1} alt="graph" /></p>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? '' : ""}</span>
        <span><p>{abstractIsVisible ? "": ''}</p></span>
      </div>      


      <p> 3. "Urna Eletrônica" (Eletronic Voting Technology) </p>
      <span><p>Fujiwara, Thomas (2015a). <a href="https://onlinelibrary.wiley.com/doi/abs/10.3982/ECTA11520" target="blank"> "Voting Technology, Political Responsiveness, and Infant Health: Evidence From Brazil"</a>. Econometrica. [<a href="http://www.princeton.edu/~fujiwara/papers/elecvote_site.pdf" target="blank">Ungated Link</a>]</p></span>
      
      <span><p>Fujiwara, Thomas (2015b). "Voting Technology, Political Responsiveness, and Infant Health: Evidence From Brazil". Econometrica, Online Appendix.[<a href="http://www.princeton.edu/~fujiwara/papers/elecvote_site_appendix.pdf" target="blank">Ungated Link</a>]</p></span>

        <p><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "''This paper studies the introduction of electronic voting technology in Brazilian elections. Estimates exploiting a regression discontinuity design indicate that electronic voting reduced residual (error‐ridden and uncounted) votes and promoted a large de facto enfranchisement of mainly less educated citizens. Estimates exploiting the unique pattern of the technology's phase‐in across states over time suggest that, as predicted by political economy models, it shifted government spending toward health care, which is particularly beneficial to the poor. Positive effects on both the utilization of health services (prenatal visits) and newborn health (low‐weight births) are also found for less educated mothers, but not for the more educated.''":''}</p></span></p>

      <p><img src={evt1} alt="graph"/></p>
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "With the adoption of the technology by some municipalities on the 1998 elections, the valid (non-residual) votes-turnout ratio increased by roughly 10 percentage points (15%), and the change was higher in places with high shares of illiterate citizens – a result consistent with the hypothesis that the ballot box would, in fact, enfranchize less educated citizens (first figure below).":''}</p></span>
      </p>
      
     

      <p><img src={evt2} alt="graph" /></p>
      <p><img src={evt3} alt="graph" /></p>       
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment on Figure{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "In the years of 1999 to 2006, the adoption of the technology across the country was associated with the election of state governments that increased in 34% the share of spending in health care (from 9.9% to 13.3% of the budget, as presented in the second figure below, in a robustness to leave-one-out state exercise). Over the same period, health care utilization and fetal health indicators in mothers without primary schooling showed improvements – for instance, the share of low-birth weights (less than 2,500 grams) decreased 7% (third figure below, robustness to leave-one-out state exercise). These figures are in the Online Appendix.":''}</p></span>
      </p>

      <p> 4. "Lei do Voto Compulsório"</p>

      <span><p>Bruce, Raphael and Lima, Rafael (2019). "Compulsory Voting and TV News Consumption ". Journal of Development Economics.</p></span>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "Compulsory voting leads to an increase in the consumption of TV news among young voters. This effect is only present for Brazil's most watched newscast, Jornal Nacional. This effect is stronger for those who hold moderate political views regarding the incumbent government.": ''}</p></span>
      </p>
      <p><img src={cons1} alt="graph" /></p>





      <div className="area">V. Trade Policy</div>

        <p>1. Liberalization between 1990 and 1995</p>

          <span><p>- Kovak, Brian (2013). <a href="https://www.aeaweb.org/articles?id=10.1257/aer.103.5.1960" target="blank">"Regional Effects of Trade Reform: What Is the Correct Measure of Liberalization?"</a>. American Economic Review.</p></span>

              <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span>
              <span><p>{abstractIsVisible ? "''A growing body of research examines the regional effects of trade liberalization using a weighted average of trade policy changes across industries. This paper develops a specific-factors model of regional economies that provides a theoretical foundation for this intuitively appealing empirical approach and also provides guidance on treatment of the nontraded sector. In the context of Brazil's early 1990s trade liberalization, I find that regions facing a 10 percentage point larger liberalization-induced price decline experienced a 4 percentage point larger wage decline. The results also confirm the empirical relevance of appropriately dealing with the nontraded sector.''": ''}</p></span>
              </div>
             <p><img src={lib1} alt="graph"/></p>
             <p><img src={lib2} alt="graph"/></p>
             <p><img src={lib3} alt="graph"/></p>




      <div className="area">Outside Brazil (Latin America)</div>

      <span >Dell, Melissa (2010). "The Persistent Effects of Peru's Mining Mita". Econometrica.</span>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <p>{abstractIsVisible ? "The borders indicate the colonial institution of mita in a sub-region of Peru, which induced a forced labor system where indigenous communities had to send one-seventh of their adult male population to work in Potosí silver and Huancavelica mercury mines. The boundaries induce discrete change that allows for a sharp regression discontinuity design using with two running variables (latitude and longitude). Melissa's papers asks, first: what are the reduced-form effects of mita on contemporary local prosperity? OK, check the figure for stunting, it's impressive, also from a magnitude of effects perspective! What can we learn about channels of institutional persistence? Very boldly, the paper questions if inequality is a useful foundation for theories of long-run growth: historical inequality increased subsequent investments in public goods. ``The evidence in this study indicates that large landowners ---while they did not aim to promote economic prosperity for the masses--- did shield individuals from exploitation by a highly extractive state and ensure public goods. Thus, it is unclear whether the Peruvian masses would have been better off if initial land inequality had been lower, and it is doubtful that initial land inequality is the most useful foundation for a theory of long-run growth. (our emphasis, p. 1866)" : ''}</p>
      </p><img src={mita1} alt="graph" />
      <img src={mita2} alt="graph" />
      <p>
        



      <div className="area">Outside Brazil (Developed Countries)</div>

      <span >Fredriksson, Peter, Öckert, Björn, Oosterbeek, Hessel. (2013). "The Long-run Effects of Class Size". Quarterly Journal of Economics.</span>
      <p><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>      </p>
        {abstractIsVisible ? "This article evaluates the long-term effects of class size in primary school using rich data from Sweden and exploiting variation in class size created by a maximum class size rule. I really like the way this table combines information on reduced-form, instrumental variables and first-stage estimates." : ''}
      </p>
      <p>
        <img src={class2} alt="graph" /> 
      </p>
    </div>
    
  )
}

export default NiceFiguresTables;
